import React, { Component } from "react";

import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { validationLokasi } from "../../Validations/validationLokasi";
import { renderField } from "../../renderField";
import { AxiosMasterGet } from "../../Axios";

const maptoState = (state) => {
  return {
    initialValues: {
      NamaLokasi: state.provinsi.editLokasi.namaLokasi,
      KodeLokasi: state.provinsi.editLokasi.kodeLokasi,
    },
  };
};
class FormLokasiEdit extends Component {
  constructor(props) {
    super(props);
    this.state = {
      response: "",
      status: "AKTIF",
      listProvinsi: [],
    };
  }

  componentDidMount() {
    AxiosMasterGet("citys")
      .then((res) =>
        this.setState({
          listProvinsi: res.data,
        })
      )
      .catch((err) => console.log(err));
  }

  render() {
    var kodenya = this.props.kode;
    var namanya = this.props.nama;
    return (
      <div>
        <form onSubmit={this.props.handleSubmit}>
          <div className="row">
            <div className="col-md-4">
              <Field
                name={kodenya.replace(/\s/g, "")}
                type="text"
                normalize={(value) => value && value.toUpperCase()}
                component={renderField}
                label={this.props.kode}
                readOnly={true}
                limit={5}
              />
            </div>
            <div className="col-md-4">
              <Field
                name={namanya.replace(/\s/g, "")}
                type="text"
                normalize={(value) => value && value.toUpperCase()}
                component={renderField}
                label={this.props.nama}
              />
            </div>

            <div className="col-lg-12 col-md-12">
              <div className="text-right">
                <button
                  type="submit"
                  id="simpan"
                  className="btn btn-primary"
                  disabled={this.props.onSend}
                >
                  {this.props.onSend ? (
                    <span
                      className="spinner-border spinner-border-sm mr-3"
                      aria-hidden="true"
                    ></span>
                  ) : null}
                  Submit
                  <i className="fa fa-paper-plane ml-3"></i>
                </button>
              </div>
            </div>
          </div>
        </form>
      </div>
    );
  }
}

FormLokasiEdit = reduxForm({
  form: "LokasiEdit", // a unique identifier for this form
  validate: validationLokasi, // <--- validation function given to redux-form
  enableReinitialize: true,
})(FormLokasiEdit);
export default connect(maptoState, null)(FormLokasiEdit);
