import React, { Component } from "react";
import AreaProvinsiKota from "./Component/AreaProvinsiKota";
import Table from "./Component/Table";
import { connect } from "react-redux";
import { editLokasi, getLokasi } from "../actions/lokasiAction";
import notrespond from "../Images/500.svg";

import Swal from "sweetalert2";
import FormLokasiEdit from "./Component/FormLokasiEdit";
import { reset } from "redux-form";
import Skeleton from "react-loading-skeleton";
import { berhasil, gagal, LogData, reactive } from "../Alert";
import { hideModal, showModal } from "../actions/ModalAction";
import { AxiosMasterDelete, AxiosMasterPost, AxiosMasterPut } from "../Axios";
import ModalMasterData from "./Component/ModalMasterData";
import HeaderCard from "../HeaderCard";
import TourGuide from "../TourGuide";
import { finishSend, progressSend } from "../actions/LogicAction";

const defaultSorted = [
  {
    dataField: "kodeLokasi",
    order: "asc",
  },
];
function hapus(dispatch, kode) {
  Swal.fire({
    title: "Anda Yakin ?",
    text: "Anda Akan Menghapus Data Ini ?",
    icon: "warning",
    showCancelButton: true,
    confirmButtonColor: "#3085d6",
    cancelButtonColor: "#d33",
    confirmButtonText: "Yes, delete it!",
  }).then((result) => {
    if (result.isConfirmed) {
      let data = {
        data: {
          kodeLokasi: kode,
        },
      };
      AxiosMasterDelete("lokasi/delete/" + kode, data)
        .then(() => dispatch(hideModal()))
        .then((res) =>
          berhasil("Data Berhasil Dihapus!").then(() => dispatch(getLokasi()))
        )
        .catch((err) =>
          gagal("Data Gagal Dihapus!").then(() =>
            LogData("Data Gagal Dihapus Di Lokasi", data, err)
          )
        );
    }
  });
}

const maptoState = (state) => {
  return {
    lokasi: state.provinsi.lokasi,
    alert: state.provinsi.alert,
    error: state.provinsi.error,
    onSend: state.provinsi.onSend,
  };
};
class Lokasi extends Component {
  constructor(props) {
    super(props);
    this.state = {
      Lokasi: [],
      response: "",
      isEdit: false,
      columns: [
        {
          dataField: "kode_lokasi",
          text: "Kode Lokasi",
          sort: true,
          headerStyle: () => {
            return { width: "15%" };
          },
        },
        {
          dataField: "nama_lokasi",
          text: "Nama Lokasi",
          sort: true,
        },
        {
          dataField: "link",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            return (
              <div className="text-center">
                <button
                  className="btn btn-danger mr-2"
                  onClick={() => hapus(this.props.dispatch, row.kode_lokasi)}
                  data-tut="reactour_3"
                >
                  <i className="fa fa-trash mr-1"></i>
                  HAPUS
                </button>
                <button
                  className="btn btn-warning"
                  data-tut="reactour_4"
                  onClick={() =>
                    this.props.dispatch(
                      editLokasi(
                        row.kode_lokasi,
                        row.nama_lokasi,
                        this.editModal()
                      )
                    )
                  }
                >
                  <i className="fa fa-edit mr-2"></i>
                  EDIT
                </button>
              </div>
            );
          },
        },
      ],
    };
  }

  componentDidMount() {
    this.props.dispatch(getLokasi());
  }

  handleSubmit(hasil) {
    const data = {
      kode_lokasi: hasil.KodeLokasi,
      nama_lokasi: hasil.NamaLokasi,
    };
    this.props.dispatch(progressSend());
    AxiosMasterPost("lokasi/add", data)
      .then(() => this.props.dispatch(finishSend()))
      .then(() => this.props.dispatch(hideModal()))
      .then((res) =>
        berhasil("Data Berhasil Disimpan!")
          .then(() => LogData("Data Berhasil Disimpan Di Lokasi", data))
          .then(() => this.props.dispatch(getLokasi()))
          .then(() => this.props.dispatch(reset("Provinsi")))
      )
      .catch((err) =>
        this.handleReactive(err, hasil.KodeLokasi, {
          nama_lokasi: hasil.NamaLokasi,
        })
      );
  }
  handleReactive(err, kode, data) {
    this.props.dispatch(hideModal());
    let error = err.response.data;
    let check = error.includes("deleted");
    check
      ? reactive(err, kode, "lokasi/reactive/", data, "lokasi/edit/").then(() =>
          this.props.dispatch(getLokasi())
        )
      : gagal("Data Gagal Ditambahkan");
  }
  handleEdit(hasil) {
    const data = {
      nama_lokasi: hasil.NamaLokasi,
    };
    this.props.dispatch(progressSend());
    AxiosMasterPut("lokasi/edit/" + hasil.KodeLokasi, data)
      .then(() => this.props.dispatch(finishSend()))
      .then(() => this.props.dispatch(hideModal()))
      .then((res) =>
        berhasil("Data Berhasil Diubah!")
          .then(() => LogData("Data Berhasil Diubah Di Lokasi", data))
          .then(() => this.props.dispatch(getLokasi()))
      )
      .catch((err) =>
        gagal("Data Gagal Diubah!")
          .then(() => LogData("Data Gagal Diubah Di Lokasi", data))
          .then(() => this.props.dispatch(hideModal()))
      );
  }
  tambahModal() {
    this.props.dispatch(showModal());
    this.setState({
      isEdit: false,
    });
  }
  editModal() {
    this.props.dispatch(showModal());
    this.setState({
      isEdit: true,
    });
  }
  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div id="notif_alert" className={this.props.alert} role="alert">
          <button
            type="button"
            className="close"
            data-dismiss="alert"
            aria-label="Close"
          >
            <span aria-hidden="true">×</span>
          </button>
          <strong>Error - </strong> Tidak Bisa Mengambil Data
        </div>
        <TourGuide
          tourConfig={[
            `Untuk Menambahkan Data Lokasi, Tekan Tombol ini Dan Isi kolom yang disediakan`,
            `Ini adalah Data Lokasi yang sudah tersimpan`,
            `Ini adalah tombol untuk menghapus data pada baris yang dipilih`,
            `Ini adalah tombol untuk mengubah data pada baris yang dipilih`,
          ]}
        />
        <div className="row">
          <div className="col-12">
            <div className="card">
              <HeaderCard title="Data Lokasi" />
              <div className="card-body">
                <ModalMasterData
                  content={
                    this.state.isEdit ? (
                      <FormLokasiEdit
                        nama="Nama Lokasi"
                        kode="Kode Lokasi"
                        from="lokasi"
                        onSubmit={(data) => this.handleEdit(data)}
                        kode_ext="Kode Ext"
                        onSend={this.props.onSend}
                      />
                    ) : (
                      <AreaProvinsiKota
                        nama="Nama Lokasi"
                        kode="Kode Lokasi"
                        from="lokasi"
                        onSubmit={(data) => this.handleSubmit(data)}
                        kode_ext="Kode Ext"
                        onSend={this.props.onSend}
                      />
                    )
                  }
                  title={
                    this.state.isEdit
                      ? "Edit Data Lokasi"
                      : "Tambah Data Lokasi"
                  }
                />
                <div className="col-lg-12 col-md-12">
                  <div className="text-right">
                    <button
                      name="simpan"
                      id="simpan"
                      type="button"
                      className="btn btn-info"
                      onClick={() => this.tambahModal()}
                      data-tut="reactour_1"
                    >
                      Tambah Data Lokasi
                    </button>
                  </div>
                </div>
                <div className="col-lg-12" data-tut="reactour_2">
                  {this.props.lokasi ? (
                    <Table
                      data={this.props.lokasi}
                      columns={this.state.columns}
                      defaultSorted={defaultSorted}
                      namaKey="kodeLokasi"
                    />
                  ) : (
                    <div>
                      {this.props.error ? (
                        <div className="text-center">
                          <img
                            src={notrespond}
                            style={{ width: "30%" }}
                            alt="NOT RESPOND"
                          />
                          <br />
                          <h4>Request API Gagal, Silahkan Refresh</h4>
                        </div>
                      ) : (
                        <Skeleton width={"100%"} height={180} />
                      )}
                    </div>
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptoState, null)(Lokasi);
